import { Component, OnInit, Injector, ViewChild, HostListener, ElementRef } from '@angular/core';
import { Router } from '@angular/router';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

import { UserService } from 'src/app/api/controllers/User';
import { StaffService } from 'src/app/api/controllers/Staff';
import { TicketService } from 'src/app/api/controllers/Ticket';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-helpdesk-unassigned',
  templateUrl: './helpdesk-unassigned.component.html',
  styleUrls: ['./helpdesk-unassigned.component.scss']
})

export class HelpdeskUnassignedComponent implements OnInit {

  ticketData:string = "";
  ticketListData:any;

  isTicketDataLoaded:boolean = false;
  totalItems:any;

  modalService:any;

  ticket:any;

  columnData:any;
  gridSourceData:any;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    private injector: Injector,
    private userService:UserService,
    private staffService:StaffService,
    private ticketService: TicketService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) {

    this.modalService = this.injector.get(ModalService);

  }

  @HostListener('window:onEditTicket', ['$event.detail'])
  onEditTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let ticketId = dataRecord.ticketId
    this.router.navigateByUrl('/ams/helpdesk/edit-ticket/' + ticketId);
  }

  @HostListener('window:onTicketDelete', ['$event.detail'])
  onTicketDelete(detail) {
    this.modalService.showConfirmModal(detail.rowId);
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'helpdeskData');
  }

  isAdmin(){
    return ( this.cookieService.get('userRole') == 'Admin' || this.cookieService.get('userRole') == 'Staff') ? true : false;
  }

  onGlSearchFilter() {

    if(this.ticketData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.ticketData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();

    }
    else {
      this.datagrid.clearfilters();
    }

  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Ticket ID',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
     }, {
         text: 'Type',
         datafield: 'ticketTypeId_Label',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell"><span class="dots rt-3 '+ getClassName(value) +'"></span></div>'
         },
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Status',
         datafield: 'ticketStatusId_Label',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell"><span class="badge small min text-capitalize '+ getClassName(value) +'">'+ value +'</span></div>'
         },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Category',
         datafield: 'ticketCategoryId_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Priority',
         datafield: 'ticketPriorityId_Label',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell"><span class="dots rt-3 '+ getClassName(value) +'"></span></div>'
         },
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Description',
         datafield: 'description',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Assigned to',
         datafield: 'assignedTo_Label',
         cellsrenderer: cellsrenderer,
         minwidth: 150,
         renderer: columnrenderer
     }, {
        text: 'Requested By',
        datafield: 'raisedby_Label',
        cellsrenderer: cellsrenderer,
        minwidth: 150,
        renderer: columnrenderer
     },{
        text: 'Date Requested',
        datafield: 'insertedOn',
        cellsrenderer: cellsrenderer,
        minwidth: 170,
        renderer: columnrenderer
     },{
         text: 'Actions',
         cellsalign: 'center',
         align: 'center',
         width: 120,
         cellsrenderer: (row: number): string => {
           return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editTicketEvent('+ row +')"><i class="fa fa-pencil icon edit" title="Edit Ticket Details" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEvent('+ row +')"><i class="fa fa-trash icon delete" title="Delete" aria-hidden="true"></i></a></div>'
         },
         renderer: columnrenderer
     }];


    if(this.cookieService.get('userRole') == 'Admin' || this.cookieService.get('userRole') == 'Staff') {

        let params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId'))
        }

        this.ticketService.getAllTicketsByApartmentId(params).subscribe((res:any) => {
          //filter active true items
          var ticketListData = res.filter(data => {
            return  (data.isActive && data.assignedTo == null);
          })
          // for (var i = 0; i < ticketListData.length; i++) {
          //   if (ticketListData[i].ticketStatusId_Label == 'New') {
          //     ticketListData[i].ticketStatusId_Label = 'inprogress';
          //   }
          // }
          this.gridSourceData = {
            localdata: ticketListData,
            datatype: "array"
          }

          this.ticketListData = new jqx.dataAdapter(this.gridSourceData);
          this.isTicketDataLoaded = true;

      },
      error => {

      }
      );

    }

    if(this.cookieService.get('userRole') == 'Tenant' || this.cookieService.get('userRole') == 'Owner') {

      let params = {
        raisedUserId: parseInt(this.cookieService.get('userId'))
      }

      this.ticketService.getTicketsByRaisedUserId(params).subscribe((res:any) => {

          //filter active true items
          var ticketListData = res.filter(data => {
            return data.isActive
          })

          this.gridSourceData = {
            localdata: ticketListData,
            datatype: "array"
          }

          this.ticketListData = new jqx.dataAdapter(this.gridSourceData);
          this.isTicketDataLoaded = true;

      },
      error => {

      }
      );

    }


    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( id => {

      if(id != null){

        let dataRecord = this.datagrid.getrowdata(id)
        let ticketId = dataRecord.ticketId

        let params = {
          ticketId: ticketId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }


        this.ticketService.deleteTicket(params).subscribe((res:any) => {

          /*_.each(this.ticketListData, (type)=>{
            if(type.ticketId == id){
              type.isActive = false;
            }
          })*/

          setTimeout(() => {

            /*this.ticketListData = this.ticketListData.filter((type) => type.ticketId !== id);*/
            this.datagrid.deleterow(id);
            this.totalItems = this.ticketListData.length;

            this.sharedService.setAlertMessage("Ticket deleted");
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)



        }, error => {
          console.log(error);
        });


      }

    });



  }


  }

  function getClassName(value){
  var name = value.split('-');
  return name[0].toLowerCase();
  }
  (window as any).getClassName = getClassName;

  function showConfirmDeleteEvent(row){
  var event = new CustomEvent('onTicketDelete', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
  }
  (window as any).showConfirmDeleteEvent = showConfirmDeleteEvent;


  function editTicketEvent(row){
  var event = new CustomEvent('onEditTicket', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
  }
  (window as any).editTicketEvent = editTicketEvent;
